.root_container {
  background-color: #141414;
  min-height: 100vh;
  margin: 0;
  padding: 0;
}

.title_section {
  background-color: #141414;
  padding: 40px 0 60px;
  position: relative;
}

.title {
  color: #ffffff;
  font-size: 2.8rem;
  font-weight: 800;
  text-align: center;
  margin: 0;
  letter-spacing: 4px;
  text-transform: uppercase;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  position: relative;
}

/* 밑줄 스타일 */
.title::after {
  content: '';
  position: absolute;
  bottom: -20px;
  left: 50%;
  transform: translateX(-50%);
  width: 40px;
  height: 4px;
  background-color: #3498db;
  border-radius: 2px;
}

@media screen and (max-width: 768px) {
  .title_section {
    padding: 30px 0 50px;
  }
  
  .title {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 480px) {
  .title_section {
    padding: 25px 0 40px;
  }
  
  .title {
    font-size: 1.8rem;
    letter-spacing: 3px;
  }
}

.container {
  height: 100%;
  display: flex;
  justify-content: center;
  background-color: #141414;
  min-height: 100vh;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 1.2rem;
  color: #ffffff;
}

.movies {
  display: grid;
  grid-template-columns: repeat(3, minmax(280px, 1fr)); /* 3열로 변경하고 최소 너비 축소 */
  grid-gap: 40px; /* 간격 축소 */
  padding: 30px; /* 패딩 축소 */
  width: 90%; /* 전체 너비 축소 */
  max-width: 1200px; /* 최대 너비 제한 */
  padding-top: 50px;
}

/* MovieInfo Component Styles */
.movie_card {
  background-color: #1f1f1f;
  border-radius: 8px; /* 모서리 반경 축소 */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  transition: transform 0.2s ease-in-out;
}

.movie_card:hover {
  transform: translateY(-8px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.4);
}

.movie_card img {
  width: 100%;
  height: auto;
  aspect-ratio: 2/3;
  object-fit: cover;
}

.movie_data {
  padding: 16px; /* 패딩 축소 */
}

.movie_data h2 {
  font-size: 1.2rem; /* 글자 크기 축소 */
  margin-bottom: 8px;
}

.movie_data h2 a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.2s ease;
}

.movie_data h2 a:hover {
  color: #3498db;
}

.movie_data p {
  color: #b3b3b3;
  font-size: 0.9rem; /* 글자 크기 축소 */
  line-height: 1.5;
  margin-bottom: 12px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}

.movie_genres {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 6px; /* 간격 축소 */
}

.movie_genres li {
  background-color: rgba(52, 152, 219, 0.2);
  color: #3498db;
  padding: 4px 8px; /* 패딩 축소 */
  border-radius: 12px;
  font-size: 0.8rem; /* 글자 크기 축소 */
  font-weight: 500;
}

/* Responsive Design */
@media screen and (max-width: 1200px) {
  .movies {
    grid-template-columns: repeat(2, minmax(260px, 1fr)); /* 2열로 변경 */
    width: 95%;
  }
}

@media screen and (max-width: 768px) {
  .movies {
    grid-template-columns: repeat(1, minmax(240px, 1fr)); /* 1열로 변경 */
    grid-gap: 30px;
    padding: 20px;
  }

  .movie_card {
    max-width: 400px; /* 카드 최대 너비 제한 */
    margin: 0 auto;
  }
}

@media screen and (max-width: 480px) {
  .movies {
    padding: 15px;
    grid-gap: 20px;
  }

  .movie_data {
    padding: 12px;
  }

  .movie_data h2 {
    font-size: 1.1rem;
  }
}