/* Detail.module.css */

.nav_section {
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  width: 100%;
}


.back_link, 
.back_link:link,
.back_link:visited {
  text-decoration: none;
  color: inherit;
}

.back_link p {
  color: #fff;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 6px;
}

.back_link p::before {
  content: '↺'; /* 동그란 화살표 기호로 변경 */
  font-size: 1.4rem; /* 화살표 크기 약간 키움 */
}

.back_link:hover p {
  color: rgba(255, 255, 255, 0.7);
}




.container {
  background-color: #141414;
  min-height: 100vh;
  padding: 40px 20px;
  color: white;
  display: flex;
  justify-content: center;
}

/* detail_wrapper 스타일 수정 */
.detail_wrapper {
  max-width: 1200px;
  width: 100%;
  display: flex;
  flex-direction: column;
  background-color: rgba(32, 32, 32, 0.9);
  padding: 30px 40px 40px;  /* 상단 패딩 살짝 줄임 */
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.3);
}

.content_wrapper {
  display: grid;
  grid-template-columns: 400px 1fr;
  gap: 50px;
}

@media screen and (max-width: 1024px) {
  .content_wrapper {
    grid-template-columns: 300px 1fr;
    gap: 30px;
  }
}

@media screen and (max-width: 768px) {
  .content_wrapper {
    grid-template-columns: 1fr;
  }
  

}

.image_container {
  position: relative;
}

.image_container img {
  width: 100%;
  height: auto;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
}

.info_container {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.title {
  font-size: 2.8rem;
  font-weight: 700;
  margin: 0;
  color: white;
}

.year {
  font-size: 1.4rem;
  color: #9e9e9e;
  font-weight: 500;
}

.rating {
  display: inline-block;
  background-color: #3498db;
  color: white;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 1.2rem;
  font-weight: 600;
}

.genres_title {
  font-size: 1.4rem;
  color: #9e9e9e;
  margin-bottom: 10px;
}

.genres_list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  margin-bottom: 20px;
}

.genres_list li {
  background-color: rgba(52, 152, 219, 0.2);
  color: #3498db;
  padding: 6px 12px;
  border-radius: 20px;
  font-size: 0.9rem;
}

.summary {
  font-size: 1.1rem;
  line-height: 1.6;
  color: #e0e0e0;
  margin-bottom: 30px;
}

.link {
  display: inline-block;
  background-color: rgba(255, 255, 255, 0.1);
  color: white;
  text-decoration: none;
  padding: 12px 24px;
  border-radius: 8px;
  font-weight: 500;
  transition: all 0.3s ease;
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.link:hover {
  background-color: rgba(255, 255, 255, 0.15);
  border-color: rgba(255, 255, 255, 0.3);
}

.loader {
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  color: white;
  font-size: 1.5rem;
}

/* 반응형 디자인 */
@media screen and (max-width: 1024px) {
  .detail_wrapper {
    grid-template-columns: 300px 1fr;
    gap: 30px;
    padding: 30px;
  }

  .title {
    font-size: 2.2rem;
  }
}

@media screen and (max-width: 768px) {
  .detail_wrapper {
    grid-template-columns: 1fr;
    max-width: 600px;
  }

  .image_container {
    max-width: 400px;
    margin: 0 auto;
  }

  .title {
    font-size: 2rem;
  }
}

@media screen and (max-width: 480px) {
  .container {
    padding: 20px 10px;
  }

  .detail_wrapper {
    padding: 20px;
  }

  .title {
    font-size: 1.8rem;
  }
}